.heading{
    text-align: center;
    font-style: italic;
    font-weight: 700;
    margin-bottom: 0px;
    
}
.menu{

    display: flex;
    list-style: none;
    justify-content: space-between;    
    margin-right: 10px;
    margin-bottom: 0px;
    margin-top: 10px;
     
}

nav ul{

    border: solid 2px;
    padding-left: 10px;
    margin: 0px 7px 6px 7px;
    
    
}
nav ul li a{
    text-decoration: none;
    color: black;
    font-weight: bolder;
    font-size: larger;
}

nav ul li a:hover{

    color: red;
    background-color: black;
}

.news1{
    
    padding: 0px 50px;
    display: inline-block;
    border: dotted 1px;
    border-style: ridge;
    margin-left: 7px;
    padding-left: 0px;
    cursor: pointer;
    
    
}
.news2{
    padding: 0px 50px;
    display: inline-block;
    border: dotted 1px;
    border-style: ridge;
    margin-right: 10px;
    padding-left: 0px;
    cursor: pointer;
    
}



.grid{
    display: grid;
    grid-template-columns: auto auto;
    flex-wrap: nowrap;
    grid-gap: 10px;
    }

.image{
    cursor: pointer;
    float: left;
    width: 100px;
   
    
  
}
h4{
    
    /* background-color: blue; */
    display: inline-block;
    text-align: center;
    margin-bottom: 0px;
    margin-top: 0px;
    font-size: large;
       
}
h4:hover{

    color:blue;
}
span:hover{
    color: blue;
}
h3{

text-decoration: underline;
font-style: italic;
text-align: center;
margin-bottom: 0px;

}

a{
    color: black;
    text-decoration: none;
}
